import { useEffect, useState } from "react";
import { styled } from "@mui/material/styles";
import { Box, Stack, IconButton, Typography, Modal } from "@mui/material";
import FavoriteIcon from "@mui/icons-material/Favorite";
import { restApi } from "../../../providers/restapi";
import userTempImg from "../../../assets/img/tanker-temp.png";
import userTempCoverImg from "../../../assets/img/tanker-cover-temp.png";
import { ActionButton2 } from "../../../components/buttons";
import { useBlockchainContext } from "../../../providers";
import discordIcon from "../../../assets/img/discordicon.png";
import linkedinIcon from "../../../assets/img/linkedinicon.png";
import telegramIcon from "../../../assets/img/telegramicon.png";
import defaultlinkIcon from "../../../assets/img/defaultlinkicon.png";
import { EditProfile } from "./editpanel";

const linkIcons: any = {
  discord: discordIcon,
  telegram: telegramIcon,
  linkedin: linkedinIcon,
  defaultlink: defaultlinkIcon,
};

const initProfile: UserProfile = {
  name: "player",
  address: "",
  email: "",
  description: "Player",
  follows: 0,
  image: "",
  coverImage: "",
  links: [],
  ranking: 0,
  merit: 0,
};

export const ProfilePanel = ({ address }: { address: string }) => {
  const [state] = useBlockchainContext();
  const [profile, setProfile] = useState<UserProfile>(initProfile);
  const [openModal, setOpenModal] = useState(false);

  const updateProfile = async () => {
    let _profile: UserProfile = await restApi.getProfile(address);
    setProfile({ ...profile, ..._profile });
  }

  useEffect(() => {
    if (!!address) {
      updateProfile();
      setTimeout(updateProfile, 5000)
    }
  }, [address]);

  return (
    <Stack
      direction="row"
      alignItems="center"
      sx={{ minHeight: "300px", width: "100%", position: "relative" }}
    >
      <BackgroundTag>
        <BackgroundImageTag
          src={profile.coverImage || userTempCoverImg}
          alt="Tanker"
        />
        <BackgroundColorTag />
      </BackgroundTag>
      <Stack
        direction={{ sm: "column", md: "row" }}
        sx={{ padding: { xs: "5px", sm: "30px" }, flex: 1 }}
        justifyContent="space-between"
        alignItems="center"
      >
        <Stack direction={{ xs: "column", sm: "row" }} spacing={4}>
          <Box
            component="img"
            src={profile.image || userTempImg}
            alt="Profile"
            sx={{
              textAlign: "center",
              width: "150px",
              height: "150px",
              border: "5px solid",
              borderColor: "primary.light",
              objectFit: "cover",
              borderRadius: "20px",
            }}
          />
          {/* profile info */}
          <Stack spacing={1.5}>
            <Typography variant="h4">{profile.name}</Typography>
            <Stack direction="row" spacing={2}>
              {profile.links.map((link: any, index: number) => (
                <Box
                  key={index}
                  component="a"
                  href={link.href || "/"}
                >
                  <Box
                    component="img"
                    src={
                      linkIcons[link.type] ||
                      linkIcons["defaultlink"]
                    }
                    alt="Profile"
                    sx={{
                      textAlign: "center",
                      width: "30px",
                      height: "30px",
                      objectFit: "cover",
                    }}
                  />
                </Box>
              ))}
            </Stack>
            <Typography>{profile.description}</Typography>
            {/* follow */}
            <Stack direction="row" spacing={1} alignItems="center">
              <IconButton aria-label="add to favorites">
                <FavoriteIcon />
              </IconButton>
              <Typography>Follows {profile.follows}</Typography>
            </Stack>
            {/* Edit profile */}
            {address && address === state.account ? (
              <ActionButton2 onClick={() => setOpenModal(true)}>
                {state.account && profile.email == "" ? "Create Profile To Play Game" : "Edit Profile"}
              </ActionButton2>
            ) : null}
          </Stack>
        </Stack>
        {/* Ranking info */}
        <Stack sx={{ minWidth: "200px" }} spacing={2}>
          <Typography>Ranking: {profile.ranking}</Typography>
          <Typography>Merit: {profile.merit}</Typography>
        </Stack>
      </Stack>

      <Modal
        open={openModal}
        onClose={() => setOpenModal(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <EditProfile profile={profile} setModal={setOpenModal} updateProfile={updateProfile} />
      </Modal>
    </Stack>
  );
};
const BackgroundTag = styled("div")(({ theme }) => ({
  backgroundColor: theme.palette.secondary.main,
  position: "absolute",
  left: 0,
  top: 0,
  width: "100%",
  height: "100%",
  zIndex: -1,
}));
const BackgroundImageTag = styled("img")(({ theme }) => ({
  position: "absolute",
  left: 0,
  top: 0,
  width: "100%",
  height: "100%",
  objectFit: "cover",
  opacity: "0.7",
}));
const BackgroundColorTag = styled("div")(({ theme }) => ({
  backgroundColor: theme.palette.secondary.main,
  position: "absolute",
  left: 0,
  top: 0,
  opacity: "0.4",
  width: "100%",
  height: "100%",
}));
