import {
  Stack,
  Box,
  Card,
  CardHeader,
  CardMedia,
  CardContent,
  CardActions,
  IconButton,
  Typography,
  LinearProgress,
  linearProgressClasses
} from "@mui/material"
import { useMemo } from "react";
import { useNavigate } from 'react-router-dom';

import FavoriteIcon from '@mui/icons-material/Favorite';
import { styled } from '@mui/material/styles';

import { useBlockchainContext } from "../../providers";
import { ActionButton1 } from "../buttons";
import { ItemCard } from "../cards";
import { restApi } from "../../providers/restapi";
import { AddNotification } from "../../utils/views";
import { styledString } from "../../utils";

enum ItemType {
  "onUser",
  "onUserMine"
};

export const TankItemCard = ({ item }: { item: TankItem }) => {
  const [state, { upgradeNFT }] = useBlockchainContext();
  const classInfo = useMemo(() => {
    return state.tankClasses.find((tankClass: TankClass) => {
      return tankClass.id === item.classType;
    })
  }, [state.tankClasses]);

  const tankInfo = useMemo(() => {
    let tankLevel = item.tankLevel;
    let currentExp = item.experience - (tankLevel) * (tankLevel) * 1000;
    let nextLevelExp = (tankLevel + 1) * (tankLevel + 1) * 1000 - (tankLevel) * (tankLevel) * 1000;
    // energy pool
    let maxEnergyPool = item.level * 1000;
    let energyPoolSize = item.energyPool + 1000;
    let energyRecoverPerHour = ((item.energyPool + 1000) / 24).toFixed(2);

    let itemType = 0;
    if (item.owner?.toUpperCase() === state.account?.toUpperCase()) itemType = ItemType.onUserMine;
    else itemType = ItemType.onUser;

    return { ...item, tankLevel, currentExp, nextLevelExp, maxEnergyPool, itemType, energyPoolSize, energyRecoverPerHour };
  }, [item])

  /* ----- actions ----- */
  const navigate = useNavigate();
  /* ----- actions ----- */
  const handleUpgrade = async () => {
    try {
      var tx = await upgradeNFT(item.id);
      await tx.wait();
      AddNotification("Success", `Upgrade ${item.id} success`, "success");
    } catch (err: any) {
      console.log("handle Upgrade", err.message);
      AddNotification(`Upgrade ${item.id} Failed`, err.message, "danger");
    }
  }

  const handleLend = async () => {
    try {
      let signature: string = await state.signer.signMessage(item.id);
      await restApi.lend(item.id, "", signature);
      AddNotification("Success", `Lend ${item.id} success`, "success");
    } catch (err: any) {
      console.log("handle lend", err.message);
      AddNotification(`Lend ${item.id} Failed`, err.message, "danger");
    }
  }
  const handleBorrow = async () => {
    try {
      if (!state.signer) throw new Error("Please connect wallet!")
      let signature: string = await state.signer.signMessage(item.id);
      await restApi.borrow(item.id, signature);
      AddNotification("Success", `borrow ${item.id} success`, "success");
    } catch (err: any) {
      console.log("handle lend", err.message);
      AddNotification(`Borrow ${item.id} Failed`, err.message, "danger");
    }
  }

  const handleLike = async () => {
    try {
      if (!state.signer) throw new Error("Please connect wallet!")
      let signature: string = await state.signer.signMessage(item.id);
      await restApi.reactNFT(item.id, signature);
      AddNotification("Success", `React ${item.id} success`, "success");
    } catch (err: any) {
      console.log("handle lend", err.message);
      AddNotification(`React ${item.id} Failed`, err.message, "danger");
    }
  }

  const handleDetail = () => {
    navigate("/detail/" + item.id);
  }

  return (
    <ItemCard>
      <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ mb: "10px" }}>
        <Box sx={{ maxWidth: "40px" }}>
          {tankInfo.level}/{tankInfo.tankLevel}
        </Box>
        <Box sx={{ flex: 1, paddingLeft: "10px" }}>
          <BorderLinearProgress variant="determinate" value={tankInfo.currentExp * 100 / tankInfo.nextLevelExp} />
        </Box>
      </Stack>
      <CardMedia
        component="img"
        height="194"
        image={tankInfo.image}
        alt="image"
        onClick={handleDetail}
      />
      <CardContent>
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <Typography>
            {tankInfo.name}
          </Typography>
          <Typography>
            {styledString(tankInfo.owner)}
          </Typography>
        </Stack>
        {/* <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <Typography>
            {classInfo?.name}
          </Typography>
          <Typography>
            {tankInfo.energyPool}/{tankInfo.maxEnergyPool}
          </Typography>
        </Stack> */}
        <Stack sx={{ mt: "10px" }}>
          <Typography>
            Energy
          </Typography>
        </Stack>
        <Stack>
          <Typography sx={{ flex: 1 }}>
            <BorderLinearProgress variant="determinate" value={tankInfo.energy * 100 / tankInfo.energyPoolSize} />
          </Typography>
        </Stack>
      </CardContent>

      <CardActions sx={{ display: "flex", justifyContent: "space-between" }}>
        <IconButton aria-label="add to favorites" onClick={handleLike}>
          <FavoriteIcon />{tankInfo.followers.length}
        </IconButton>
        {tankInfo.itemType === ItemType.onUserMine ? (
          <Stack spacing={2} direction="row">
            {/* <ActionButton1 disabled={tankInfo.level >= tankInfo.tankLevel} onClick={handleUpgrade}>Upgrade</ActionButton1> */}
            {tankInfo.borrower.toUpperCase() === tankInfo.owner.toUpperCase() ? (
              <ActionButton1 onClick={handleLend}>Lend</ActionButton1>
            ) : (
              <ActionButton1 onClick={handleBorrow}>retrieve</ActionButton1>
            )}
          </Stack>) : (
          <Stack spacing={2} direction="row">
            {tankInfo.borrower.toUpperCase() === "" ? (<ActionButton1 onClick={handleBorrow}>Borrow</ActionButton1>) : ""}
            {tankInfo.borrower.toUpperCase() === state.account?.toUpperCase() ? (<ActionButton1 onClick={handleLend}>Return</ActionButton1>) : ""}
          </Stack>)
        }
      </CardActions>
    </ItemCard>
  )
}

const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
  height: 20,
  borderRadius: 5,
  [`&.${linearProgressClasses.colorPrimary}`]: {
    backgroundColor: theme.palette.secondary.main,
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 5,
    backgroundColor: theme.palette.secondary.light
  },
}));