import { UseWalletProvider } from "use-wallet";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Box from "@mui/material/Box";
import { styled } from "@mui/material/styles";
import "react-notifications-component/dist/theme.css";
import { ReactNotifications } from "react-notifications-component";

import BlockchainProvider from "./providers";
import ThemeProvider from "./providers/theme";
import NavBar from "./components/navbar";
import Sidebar from "./components/sidebar";
import BGIMG from "./assets/img/tank-bg.jpg";

import MintPage from "./views/mint";
import MyTankPage from "./views/mytanks";
import LendingPage from "./views/lending";
import ItemDetail from "./views/itemDetail";
import ClassDetail from "./views/classDetail";
import PoolsPage from "./views/pools";
import GamePage from "./views/game";
import RewardsPage from "./views/rewards";
import NotFoundPage from "./views/404page";

const Providers = ({ children }: any) => {
    return (
        <ThemeProvider>
            <ReactNotifications />
            <UseWalletProvider
                autoConnect={true}
                connectors={{
                    injected: { chainId: [4002] },
                    walletconnect: {
                        rpc: {
                            1: "https://main-light.eth.linkpool.io/",
                            3: "https://rpc-url",
                            4002: "https://ftm-test.babylonswap.finance",
                        },
                    },
                }}
            >
                <BlockchainProvider>{children}</BlockchainProvider>
            </UseWalletProvider>
        </ThemeProvider>
    );
};
function App() {
    return (
        <Box sx={{ display: "flex" }}>
            <Providers>
                <BackgroundTag>
                    <BackgroundImageTag src={BGIMG} alt="" />
                    <BackgroundColorTag />
                </BackgroundTag>
                <Router>
                    <Sidebar />
                    <NavBar />
                    <Box
                        sx={{
                            marginTop: "80px",
                            paddingInline: { sm: "30px" },
                            flex: 1,
                        }}
                    >
                        <Routes>
                            <Route path="/" element={<MyTankPage />} />
                            <Route path="/create" element={<MintPage />} />
                            <Route path="/my-tanks" element={<MyTankPage />} />
                            <Route
                                path="/user/:address"
                                element={<MyTankPage />}
                            />
                            <Route path="/lending" element={<LendingPage />} />
                            <Route
                                path="/detail/:id"
                                element={<ItemDetail />}
                            />
                            <Route
                                path="/class-detail/:id"
                                element={<ClassDetail />}
                            />
                            <Route path="/pools" element={<PoolsPage />} />
                            <Route path="/play" element={<GamePage />} />
                            <Route path="/rewards" element={<RewardsPage />} />
                            <Route path="*" element={<NotFoundPage />} />
                        </Routes>
                    </Box>
                </Router>
            </Providers>
        </Box>
    );
}

const BackgroundTag = styled("div")(({ theme }) => ({
    backgroundColor: theme.palette.secondary.main,
    position: "fixed",
    left: 0,
    top: 0,
    width: "100%",
    height: "100%",
    zIndex: -1,
}));

const BackgroundImageTag = styled("img")(({ theme }) => ({
    position: "fixed",
    left: 0,
    top: 0,
    width: "100%",
    height: "100%",
    objectFit: "cover",
    opacity: "0.4",
}));

const BackgroundColorTag = styled("div")(({ theme }) => ({
    backgroundColor: theme.palette.secondary.main,
    position: "fixed",
    left: 0,
    top: 0,
    opacity: "0.7",
    width: "100%",
    height: "100%",
}));

export default App;
