import { useState, useMemo } from "react";
import {
  Box,
  Accordion as MuiAccordion,
  AccordionProps,
  AccordionSummary as MuiAccordionSummary,
  AccordionSummaryProps,
  AccordionDetails as MuiAccordionDetails,
  Typography,
  Grid,
  TextField as MuiTextField,
  CircularProgress,
  Slider
} from '@mui/material';
import { styled } from "@mui/material/styles";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { styledString } from "../../../utils";
import { GridItem } from "../../../components/layout";
import { getValue, Stack } from "@mui/system";
import { ActionButton1 } from "../../../components/buttons";
import { useBlockchainContext } from "../../../providers";
import { AddNotification } from "../../../utils/views";

const PoolItem = (props: any) => {
  const { item, index, expanded, handleExpand } = props;
  const [state, { stake, unstake }] = useBlockchainContext();

  const itemInfo = useMemo(() => {
    let maxEnergyPool = item.level * 10000;
    return { ...item, maxEnergyPool };
  }, [item]);

  const handleStake = async (value: number) => {
    if (!state.signer) throw new Error("Please connect wallet!")
    await stake(item.id, value);
  }

  const handleUnstake = async (value: number) => {
    if (!state.signer) throw new Error("Please connect wallet!")
    await unstake(item.id, value);
  }

  return (
    <Accordion expanded={expanded === `expand${index}`} onChange={handleExpand(`expand${index}`)} key={index}>
      <AccordionSummary sx={{ minHeight: "60px" }}>
        <Grid container spacing={2} >
          <GridItem>
            <Box
              component="img"
              src={itemInfo.image}
              alt="Property"
              sx={{
                textAlign: "center",
                width: "30px",
                pr: 2
              }}
            />
            <Typography>
              {itemInfo.name}
            </Typography>
          </GridItem>
          <GridItem>
            <Typography>
              {styledString(itemInfo.owner)}
            </Typography>
          </GridItem>
          <GridItem>
            <Typography>
              {state.stakes[item.id] ? state.stakes[item.id] : "0"}
            </Typography>
          </GridItem>
          <GridItem>
            <Typography>
              {itemInfo.energyPool}/{itemInfo.maxEnergyPool}
            </Typography>
          </GridItem>
        </Grid >
      </AccordionSummary >
      <AccordionDetails>
        <Grid container spacing={2} sx={{ padding: "20px" }}>
          <Grid item xs={12} md={6} textAlign="center">
            <NumberInputPanel label={"DFTL"} actionLabel={"Stake"} max={(Number(state.balance)) < (itemInfo.maxEnergyPool - itemInfo.energyPool) ? (state.balance) : (itemInfo.maxEnergyPool - itemInfo.energyPool)} balance={state.balance} onAction={handleStake} />
          </Grid>
          <Grid item xs={12} md={6} textAlign="center" >
            <NumberInputPanel label={"SDFTL"} actionLabel={"Withdraw"} max={state.stakes[item.id] ? state.stakes[item.id] : 0} balance={state.stakes[item.id] ? state.stakes[item.id] : 0} onAction={handleUnstake} />
          </Grid>
        </Grid>
      </AccordionDetails>
    </Accordion >
  )
}

const Accordion = styled((props: AccordionProps) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  margin: "5px",
  borderRadius: "5px",
  border: `1px solid ${theme.palette.divider}`,
  '&:not(:last-child)': {
    borderBottom: 0,
  },
  '&:before': {
    display: 'none',
  },
}));

const AccordionSummary = styled((props: AccordionSummaryProps) => (
  <MuiAccordionSummary
    expandIcon={<ExpandMoreIcon sx={{ fontSize: '1.8rem' }} />}
    {...props}
  />
))(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === 'dark'
      ? 'rgba(255, 255, 255, .05)'
      : 'rgba(0, 0, 0, .03)',
  flexDirection: 'row',
  '& .MuiAccordionSummary-content': {
    marginLeft: theme.spacing(1),
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  padding: theme.spacing(2),
  borderTop: '1px solid rgba(0, 0, 0, .125)',
}));

const TextField = styled(MuiTextField)(({ theme }) => ({
  "& input::-webkit-outer-spin-button, & input::-webkit-inner-spin-button": {
    display: "none",
  },
  "& input[type=number]": {
    MozAppearance: "textfield",
  },
}));

const NumberInputPanel = ({ label, actionLabel, balance, max, onAction }: { label: string, actionLabel: string, balance: number, max: number, onAction: CallableFunction }) => {

  const [value, setValue] = useState(0);
  const [isLoading, setLoading] = useState(false);

  const handleInput = (e: any) => {
    if (e.target.value < 0) return setValue(0);
    if (e.target.value > max) return setValue(max);
    setValue(e.target.value)
  }

  const handleAction = async () => {
    setLoading(true);
    try {
      await onAction(value);
      AddNotification(`${actionLabel} Success`, "", "success")
    } catch (err: any) {
      AddNotification(`${actionLabel} failed`, "", "danger")
    }
    setLoading(false)
  }

  const marks = useMemo(() => {
    let data = [];
    for (let i = 0; i <= 4; i++)
      data.push({
        value: i * 25,
        label: `${i * 25}%`
      })
    return data;
  }, []);

  return (
    <Stack alignItems="center" spacing={2}>
      <Box width="100%" display="flex">
        <TextField
          id="outlined-number"
          label={`${label} : ${balance}`}
          type="number"
          InputLabelProps={{
            shrink: true,
          }}
          sx={{ flex: 1, maxWidth: "500px", marginInline: "auto" }}
          value={value}
          onChange={handleInput}
        />
      </Box>
      <Box width="100%" display="flex">
        <Slider
          aria-label="Small"
          sx={{ flex: 1, maxWidth: "500px", marginInline: "auto" }}
          min={0}
          max={100}
          disabled={Number(max) === 0}
          value={value * 100 / max}
          onChange={(e: any) => { setValue(e.target.value * max / 100) }}
          marks={marks}
        ></Slider>
      </Box>
      <Box sx={{ position: 'relative' }}>
        <ActionButton1 disabled={isLoading} onClick={handleAction}>{actionLabel}</ActionButton1>
        {isLoading && (
          <CircularProgress
            size={24}
            sx={{
              color: "primary.main",
              position: 'absolute',
              top: '50%',
              left: '50%',
              marginTop: '-12px',
              marginLeft: '-12px',
            }}
          />
        )}
      </Box>
    </Stack>
  )
}
export default PoolItem;