import { useState, useMemo, SyntheticEvent, useCallback } from "react";
import {
    Box,
    Stack,
    TextField,
    FormControl,
    InputLabel,
    MenuItem,
    Grid,
    Typography,
} from "@mui/material";
import { useParams } from "react-router-dom";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import { useBlockchainContext } from "../../providers";
import PoolItem from "./components";
import { GridItem } from "../../components/layout";

const PoolsPage = () => {
    const { address } = useParams();
    const [state] = useBlockchainContext();
    const [sort, setSort] = useState("");
    const [filter, setFilter] = useState("");

    const sortBy = useCallback(
        (a: TankItem, b: TankItem) => {
            var res: boolean = true;
            switch (sort) {
                case "priceLH":
                    res = Number(a.price) > Number(b.price);
                    break;
                case "priceHL":
                    res = Number(a.price) < Number(b.price);
                    break;
                case "speedLH":
                    res = Number(a.speed) > Number(b.speed);
                    break;
                case "speedHL":
                    res = Number(a.speed) < Number(b.speed);
                    break;
                case "healthLH":
                    res = Number(a.health) > Number(b.health);
                    break;
                case "healthHL":
                    res = Number(a.health) < Number(b.health);
                    break;
                case "fireRateLH":
                    res = Number(a.fireRate) > Number(b.fireRate);
                    break;
                case "fireRateHL":
                    res = Number(a.fireRate) < Number(b.fireRate);
                    break;
                case "firePowerLH":
                    res = Number(a.firePower) > Number(b.firePower);
                    break;
                case "firePowerHL":
                    res = Number(a.firePower) < Number(b.firePower);
                    break;
            }
            return res ? 1 : -1;
        },
        [sort]
    );

    const tankItems = useMemo(() => {
        return state.tankItems
            .filter((item: TankItem) => {
                return item.name?.indexOf(filter.toLowerCase().trim()) > -1;
            })
            .sort(sortBy);
    }, [state.tankItems, filter, sort]);

    const [expanded, setExpanded] = useState<string | false>(false);
    const handleExpand =
        (panel: string) => (event: SyntheticEvent, isExpanded: boolean) => {
            setExpanded(isExpanded ? panel : false);
        };

    return (
        <Box>
            <Stack
                marginTop={"30px"}
                spacing={2}
                direction={{ xs: "column", sm: "row" }}
                justifyContent="space-between"
                alignItems="center"
            >
                <TextField
                    id="Search-basic"
                    label="Search"
                    variant="outlined"
                    sx={{ width: { xs: "100%" }, maxWidth: "600px", flex: 1 }}
                    value={filter}
                    onChange={(e: any) => setFilter(e.target.value)}
                />
                <FormControl
                    sx={{ width: { xs: "100%" }, maxWidth: "400px", flex: 1 }}
                    size="medium"
                >
                    <InputLabel id="sort-select">Sort</InputLabel>
                    <Select
                        labelId="sort-select"
                        id="sort-select"
                        value={sort}
                        label="Sort"
                        onChange={(e: any) => setSort(e.target.value)}
                    >
                        <MenuItem value="">
                            <em>None</em>
                        </MenuItem>
                        <MenuItem value={"speedLH"}>Speed(Low-High)</MenuItem>
                        <MenuItem value={"speedHL"}>Speed(High-Low)</MenuItem>
                        <MenuItem value={"priceLH"}>Price(Low-High)</MenuItem>
                        <MenuItem value={"priceHL"}>Price(High-Low)</MenuItem>
                        <MenuItem value={"healthLH"}>Health(Low-High)</MenuItem>
                        <MenuItem value={"healthHL"}>Health(High-Low)</MenuItem>
                        <MenuItem value={"fireRateLH"}>
                            FireRate(Low-High)
                        </MenuItem>
                        <MenuItem value={"fireRateHL"}>
                            FireRate(High-Low)
                        </MenuItem>
                        <MenuItem value={"firePowerLH"}>
                            FirePower(Low-High)
                        </MenuItem>
                        <MenuItem value={"firePowerHL"}>
                            FirePower(High-Low)
                        </MenuItem>
                    </Select>
                </FormControl>
            </Stack>
            <Stack>
                <Stack direction="row">
                    <Grid
                        container
                        spacing={2}
                        sx={{ paddingInline: "30px", mt: "10px" }}
                    >
                        <GridItem>
                            <Typography>Tank</Typography>
                        </GridItem>
                        <GridItem>
                            <Typography>Owner</Typography>
                        </GridItem>
                        <GridItem>
                            <Typography>SDFTL</Typography>
                        </GridItem>
                        <GridItem>
                            <Typography>Staked/Capacity</Typography>
                        </GridItem>
                    </Grid>
                    <Box sx={{ width: "20px" }}></Box>
                </Stack>
                {tankItems.map((tankItem: TankItem, index: any) => (
                    <PoolItem
                        item={tankItem}
                        index={index}
                        key={index}
                        expanded={expanded}
                        handleExpand={handleExpand}
                    />
                ))}
            </Stack>
        </Box>
    );
};
export default PoolsPage;
