import React, {
  createContext,
  useContext,
  useReducer,
  useMemo,
  useEffect,
} from "react";
import { ethers } from "ethers";
import { useWallet } from "use-wallet";

import { toBigNum, fromBigNum } from "../utils";
import {
  provider, multicallProvider,
  NFTTANK,
  EnergyPool,
  TANKTOKEN,
  EnergyPool_m,
  multicallHelper
} from "../contract";
import TempDatas from "./temp.json"
import { restApi } from "./restapi";

// create context
const BlockchainContext = createContext<any>({});

function reducer(state: any, { type, payload }: { type: any, payload: any }) {
  return {
    ...state,
    [type]: payload,
  };
}

const INIT_STATE: {
  balance: number,
  signer: any,
  provider: any,
  account: string | null,

  tankClasses: TankClass[],
  tankItems: TankItem[],
  mobileOpen: boolean,

  allowances: {
    toTank: any,
    toPool: any
  },

  stakes: any,
  stakeRate: number
} = {
  balance: 0,
  signer: null,
  provider: null,
  account: null,
  tankClasses: [],
  tankItems: [],
  mobileOpen: false,
  allowances: {
    toTank: 0,
    toPool: 0
  },
  stakes: {},
  stakeRate: 1
};

// use contexts
export function useBlockchainContext() {
  return useContext(BlockchainContext);
}


export default function Provider({ children }: any) {
  const wallet = useWallet();
  const [state, dispatch] = useReducer(reducer, INIT_STATE);

  // set signer
  useEffect(() => {
    (async () => {
      if (wallet.status === "connected") {
        const provider = new ethers.providers.Web3Provider(
          wallet.ethereum
        );
        const signer = await provider.getSigner();
        dispatch({
          type: "signer",
          payload: signer,
        });
        dispatch({
          type: "provider",
          payload: provider,
        });
        dispatch({
          type: "account",
          payload: wallet.account,
        });
      } else {
        dispatch({
          type: "signer",
          payload: null,
        });
        dispatch({
          type: "account",
          payload: null,
        });
      }
    })();
  }, [wallet.status])

  /* ------- blockchain interaction functions ------- */
  // interact with smart contract
  const checkAllowance = async (to: string) => {
    if (!state.signer) throw new Error("Wallet isn't connected!")
    return await TANKTOKEN.connect(state.signer).alloance(state.account, to);
  }
  const approveToken = async (to: string, value: string | number) => {
    if (!state.signer) throw new Error("Wallet isn't connected!")
    let tx = await TANKTOKEN.connect(state.signer).approve(to, toBigNum(value, 18));
    await tx.wait();
    updateBalanceAndAlloance();
    return tx;
  }
  const mintNFT = async (id: string, price: number) => {
    if (!state.signer) throw new Error("Wallet isn't connected!");
    if (state.allowances.toTank < price) {
      await approveToken(NFTTANK.address, price * 100);
    }
    var tx = await NFTTANK.connect(state.signer).mint(String(id));
    return tx;
  }
  const upgradeNFT = async (id: string) => {
    const { availableLevel, signature }: { availableLevel: number, signature: string } = (await restApi.getUpgradeSignature(id)).data;
    if (!state.signer) throw new Error("Wallet isn't connected!")
    console.log(String(id), String(availableLevel), signature);
    let tx = await NFTTANK.connect(state.signer).upgrade(String(id), String(availableLevel), signature);
    return tx;
  }
  const stake = async (id: string, amount: number) => {
    if (state.allowances.toPool < amount) {
      await approveToken(EnergyPool.address, amount * 100);
    }
    var tx = await EnergyPool.connect(state.signer).stake(id, toBigNum(amount));
    await tx.wait();
    updatePoolStatus();
  }
  const unstake = async (id: string, amount: number) => {
    var tx = await EnergyPool.connect(state.signer).unstake(id, toBigNum(amount));
    await tx.wait();
    updatePoolStatus();
  }
  /* ------- data caching functions ------- */
  const updateTankClasses = async () => {
    try {
      const tempDatas: TankClass[] = (await restApi.getTankClasses()).data;
      dispatch({
        type: "tankClasses",
        payload: tempDatas,
      });
    } catch (err: any) {
      console.log("provider/tankClasses", err.message);
    }
  }
  const updateTankItems = async () => {
    try {
      const tempDatas: TankItem[] = (await restApi.getTankItems()).data;
      dispatch({
        type: "tankItems",
        payload: tempDatas,
      });
    } catch (err: any) {
      console.log("provider/tankClasses", err.message);
    }
  }
  const updateBalanceAndAlloance = async () => {
    const allowanceToTankContract = await TANKTOKEN.connect(state.signer).allowance(state.account, NFTTANK.address);
    const allowanceToPoolContract = await TANKTOKEN.connect(state.signer).allowance(state.account, EnergyPool.address);
    const balance = await TANKTOKEN.connect(state.signer).balanceOf(state.account);
    dispatch({
      type: "allowances",
      payload: {
        toTank: fromBigNum(allowanceToTankContract, 18),
        toPool: fromBigNum(allowanceToPoolContract, 18)
      }
    })
    dispatch({
      type: "balance",
      payload: fromBigNum(balance, 18),
    })
  }
  const updatePoolStatus = async () => {
    try {
      if (!state.account) return;
      let calls: any = state.tankItems.map((tankItem: TankItem) => {
        return EnergyPool_m.balanceOf(state.account, tankItem.id);
      });
      let res = await multicallHelper(calls);

      let stakes: any = {}
      state.tankItems.map((tankItem: TankItem, index: number) => {
        stakes[tankItem.id] = fromBigNum(res[index], 18)
      })
      console.log("stakes", stakes);
      dispatch({
        type: "stakes",
        payload: stakes
      })
    } catch (err: any) {
      console.log("providers/index/updatePoolStatus", err.message);
      dispatch({
        type: "stakes",
        payload: {}
      })
    }
  }

  const updator = () => {
    updateTankClasses();
    updateTankItems();
  }
  useEffect(() => {
    updator();
    setInterval(updateTankItems, 5000)
  }, [])
  useEffect(() => {
    if (state.account) {
      updateBalanceAndAlloance()
      updatePoolStatus()
    }
  }, [state.account])
  
  return (
    <BlockchainContext.Provider
    value={useMemo(
        () => [
          state,
          {
            dispatch,
            checkAllowance,
            approveToken,
            mintNFT,
            upgradeNFT,
            stake,
            unstake
          },
        ],
        [state]
      )}
    >
      {children}
    </BlockchainContext.Provider >
  );
}
