import axios from "axios";
axios.defaults.baseURL = "";

export const restApi = {
  // view api
  getTankClasses: async () => {
    let result = await axios.post("/api/tanks/classes");
    return result.data;
  },
  getTankItems: async () => {
    let result = await axios.post("/api/tanks/all-tanks");
    return result.data;
  },
  getProfile: async (address: string) => {
    let result = await axios.post("/api/auth/get-userdata", {
      address: address,
    });
    return result.data;
  },
  getAllProfiles: async () => {
    let result = await axios.post("/api/auth/get-alldata");
    return result.data;
  },
  //auth
  setProfile: async (data: any) => {
    let result = await axios.post("/api/auth/update-userdata", data);
    return result.data;
  },
  //auth
  checkProfile: async (data: any) => {
    let result = await axios.post("/api/auth/check-userdata", data);
    return result.data;
  },
  getUpgradeSignature: async (id: string) => {
    let result = await axios.post("/api/tanks/get-upgradesign", { id: id });
    return result.data;
  },
  borrow: async (id: string, signature: string) => {
    let result = await axios.post("/api/tanks/borrow", {
      id: id,
      signature: signature,
    });
    return result.data;
  },
  lend: async (id: string, to: string, signature: string) => {
    let result = await axios.post("/api/tanks/lend", {
      id: id,
      to: to,
      signature: signature,
    });
    return result.data;
  },
  reactNFT: async (id: string, signature: string) => {
    let result = await axios.post("/api/tanks/like", {
      id: id,
      signature: signature,
    });
    return result.data;
  },
};
