import {
  Stack,
  Box,
  Typography,
  LinearProgress,
  linearProgressClasses,
  Modal
} from "@mui/material"
import { useMemo, useState } from "react";
import { useParams, useNavigate } from 'react-router-dom';
import { styled } from "@mui/material/styles";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

import { useBlockchainContext } from "../../providers";
import { ActionButton2 } from "../../components/buttons";
import { styledString } from "../../utils";

import firePowerIcon from '../../assets/img/powericon.png';
import fireRateIcon from '../../assets/img/firerateicon.png';
import speedIcon from '../../assets/img/speedicon.png';
import healthIcon from '../../assets/img/healthicon.png';
import { restApi } from "../../providers/restapi";
import { AddNotification } from "../../utils/views";
import { LendPanel } from "./components/lendpanel";

enum ItemType {
  "onUser",
  "onUserMine"
};

export const ItemDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [state, { upgradeNFT }] = useBlockchainContext();

  const item = state.tankItems.find((tankItem: TankItem) => {
    return Number(tankItem.id) === Number(id);
  })
  const classInfo = state.tankClasses.find((tankClass: TankClass) => {
    return tankClass.id === item?.classType;
  })

  const tankInfo = useMemo(() => {
    if (!item) return null;
    let tankLevel = item.tankLevel;
    console.log(item.experience, (tankLevel) * (tankLevel) * 1000, tankLevel);
    let currentExp = item.experience - (tankLevel) * (tankLevel) * 1000;
    let nextLevelExp = (tankLevel + 1) * (tankLevel + 1) * 1000 - (tankLevel) * (tankLevel) * 1000;

    // energy pool
    let maxEnergyPool = item.level * 10000;
    let energyPoolSize = item.energyPool + 1000;
    let energyRecoverPerHour = ((item.energyPool + 100) * 10 / 24).toFixed(2);

    let itemType = 0;
    if (item.owner?.toUpperCase() === state.account?.toUpperCase()) itemType = ItemType.onUserMine;
    else itemType = ItemType.onUser;

    return { ...item, tankLevel, currentExp, nextLevelExp, maxEnergyPool, itemType, energyPoolSize, energyRecoverPerHour };
  }, [item])

  /* ----- actions ----- */
  const handleUpgrade = async () => {
    try {
      var tx = await upgradeNFT(Number(item.id));
      await tx.wait();
      AddNotification("Success", `Upgrade ${item.id} success`, "success");
    } catch (err: any) {
      console.log("handle Upgrade", err.message);
      AddNotification(`Upgrade ${item.id} Failed`, err.message, "danger");
    }
  }

  const handleLend = async () => {
    try {
      let signature: string = await state.signer.signMessage(item.id);
      await restApi.lend(item.id, "", signature);
      AddNotification("Success", `Lend ${item.id} success`, "success");
    } catch (err: any) {
      console.log("handle lend", err.message);
      AddNotification(`Lend ${item.id} Failed`, err.message, "danger");
    }
  }
  const handleBorrow = async () => {
    try {
      if (!state.signer) throw new Error("Please connect wallet!")
      let signature: string = await state.signer.signMessage(item.id);
      await restApi.borrow(item.id, signature);
      AddNotification("Success", `borrow ${item.id} success`, "success");
    } catch (err: any) {
      console.log("handle lend", err.message);
      AddNotification(`Borrow ${item.id} Failed`, err.message, "danger");
    }
  }

  // return to 404 page if item is not exist
  if (!item) {
    console.log(item, "return");
    if (state.tankItems.length !== 0) navigate('/404');
    return (<></>);
  };

  return (
    <Box>
      <ActionButton2 onClick={() => { navigate(-1) }} sx={{ m: "5px" }}><ArrowBackIcon /> back</ActionButton2>
      <StyledPanel>
        <SubPanel>
          <Stack direction="column">
            <Row>
              <Typography variant="h4">
                {tankInfo.name}
              </Typography>
            </Row>
            <Row>
              <Typography>
                Type: {classInfo.name}
              </Typography>
              <Typography>
                ID: {tankInfo.id}
              </Typography>
            </Row>
            <Row sx={{ pt: "30px", pb: "20px" }}>
              <Typography>
                Owner : {styledString(item.owner)}
              </Typography>
              {tankInfo.itemType === ItemType.onUserMine ? (
                <Stack spacing={2} direction="row">
                  <ActionButton2 disabled={tankInfo.level >= tankInfo.tankLevel} onClick={handleUpgrade}>Upgrade</ActionButton2>
                  {tankInfo.borrower.toUpperCase() === tankInfo.owner.toUpperCase() ? (
                    <ActionButton2 onClick={handleLend}>Lend</ActionButton2>
                  ) : (
                    <ActionButton2 onClick={handleBorrow}>retrieve</ActionButton2>
                  )}
                </Stack>) : (
                <Stack spacing={2} direction="row">
                  {tankInfo.borrower.toUpperCase() === "" ? (<ActionButton2 onClick={handleBorrow}>Borrow</ActionButton2>) : ""}
                  {tankInfo.borrower.toUpperCase() === state.account?.toUpperCase() ? (<ActionButton2 onClick={handleLend}>Return</ActionButton2>) : ""}
                </Stack>)
              }
            </Row>
            {/* Level and experience */}
            <Row sx={{ mt: "10px" }}>
              <Typography>
                Level
              </Typography>
              <Typography>
                {tankInfo.currentExp}/{tankInfo.nextLevelExp} {"(EXP)"}
              </Typography>
            </Row>
            <Row>
              <Typography sx={{ maxWidth: "50px", flex: 1 }}>
                {tankInfo.level}/{tankInfo.tankLevel}
              </Typography>
              <Typography sx={{ flex: 1 }}>
                <BorderLinearProgress variant="determinate" value={tankInfo.currentExp * 100 / tankInfo.nextLevelExp} />
              </Typography>
            </Row>
            <Row sx={{ height: { lg: "500px" } }}>
              <Box
                component="img"
                src={tankInfo.image}
                alt="DeFiTankLand"
                sx={{
                  textAlign: "center",
                  padding: { md: "50px" },
                  width: { md: "calc(100% - 100px)", xs: "100%" },
                  maxHeight: "400px",
                  mt: 3,
                  marginInline: "auto"
                }}
              />
            </Row>
          </Stack>
        </SubPanel>
        <SubPanel>
          <Stack direction="column">
            <Stack direction="column">
              <Typography variant="h5">
                Description
              </Typography>
              <Typography>
                {tankInfo.description}
              </Typography>
            </Stack>
            {/* Staking pool info */}
            <Row sx={{ mt: "15px" }}>
              <Typography>
                Staking Pool
              </Typography>
              <Typography>
                {tankInfo.energyPool}/{tankInfo.maxEnergyPool}
              </Typography>
            </Row>
            <Row>
              <Typography sx={{ flex: 1 }}>
                <BorderLinearProgress variant="determinate" value={tankInfo.energyPool * 100 / tankInfo.maxEnergyPool} />
              </Typography>
            </Row>
            {/* Energy pool info */}
            <Row sx={{ mt: "10px" }}>
              <Typography>
                Energy
              </Typography>
              <Typography>
                {tankInfo.energy}/{tankInfo.energyPoolSize} {"(+" + tankInfo.energyRecoverPerHour + "/h)"}
              </Typography>
            </Row>
            <Row>
              <Typography sx={{ flex: 1 }}>
                <BorderLinearProgress variant="determinate" value={tankInfo.energy * 100 / tankInfo.energyPoolSize} />
              </Typography>
            </Row>
            {/* Property */}
            <Row sx={{ mt: "30px" }}>
              <Typography variant="h5">
                Properties
              </Typography>
            </Row>
            <PropertyPanel id={id} />
          </Stack>
        </SubPanel>
      </StyledPanel>
    </Box>
  )
}

const PropertyPanel = ({ id }: { id: string | undefined }) => {
  const [state] = useBlockchainContext();
  const item: TankItem = state.tankItems.find((tankItem: TankItem) => {
    return Number(tankItem.id) === Number(id);
  })
  const classInfo: TankClass = state.tankClasses.find((tankClass: TankClass) => {
    return tankClass.id === item?.classType;
  })
  const properties = useMemo(() => {
    let properties = [];
    // fire power
    properties.push({
      icon: firePowerIcon,
      name: "Fire Power",
      value: item.firePower,
      add: classInfo.firePowerAdd
    });
    //fire rate
    properties.push({
      icon: fireRateIcon,
      name: "Fire Rate",
      value: item.fireRate,
      add: classInfo.fireRateAdd
    });
    //speed
    properties.push({
      icon: speedIcon,
      name: "Speed",
      value: item.speed,
      add: classInfo.speedAdd
    });
    //health
    properties.push({
      icon: healthIcon,
      name: "Health",
      value: item.health,
      add: classInfo.healthAdd
    });
    return properties;
  }, [item, classInfo])

  return (
    <Stack>
      {properties.map((property: any, index) => (
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
          sx={{
            border: "1px solid",
            borderColor: "primary.dark",
            m: 1,
            p: 1.5,
            borderRadius: 2
          }}
          key={index}
        >
          <Stack
            direction="row"
            justifyContent="flex-start"
            alignItems="center"
          >
            <Box
              component="img"
              src={property.icon}
              alt="Property"
              sx={{
                textAlign: "center",
                width: "40px",
                pr: 2
              }}
            />
            <Typography>
              {property.name}
            </Typography>
          </Stack>
          <Typography>
            {property.value}{"(+" + property.add + ")"}
          </Typography>
        </Stack>
      ))}
    </Stack>
  )
}

const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
  height: 20,
  borderRadius: 5,
  [`&.${linearProgressClasses.colorPrimary}`]: {
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 5,
    backgroundColor: theme.palette.primary.dark
  },
}));

const StyledPanel = styled("div")(({ theme }) => ({
  borderRadius: 5,
  backgroundColor: theme.palette.secondary.main,
  border: `1px solid ${theme.palette.primary.dark}`,
  display: "flex",
  flexWrap: "wrap"
}));

const SubPanel = styled("div")(({ theme }) => ({
  [theme.breakpoints.down('sm')]: {
    minWidth: "200px",
    padding: "20px",
  },
  [theme.breakpoints.up('sm')]: {
    padding: "50px",
    minWidth: "400px",
  },
  flex: 1
}));

const Row = styled((props: any) => (
  <Stack
    direction="row"
    justifyContent="space-between"
    alignItems="center"
    {...props}
  />
))(({ theme }) => ({
  paddingBottom: "7px"
}));

export default ItemDetail;