import { useState, useMemo, useCallback } from "react";
import {
  Box, Stack, TextField, Grid,
  FormControl,
  InputLabel,
  MenuItem,
  Select
} from "@mui/material";
import { useParams } from "react-router-dom";

import { useBlockchainContext } from "../../providers";

import { ProfilePanel } from "./components/profile";
import { TankItemCard } from "../../components/tankCard";

const MyTankPage = () => {
  const { address } = useParams();
  const [state] = useBlockchainContext();
  const [filter, setFilter] = useState("");
  const [type, setType] = useState("All");

  const userAddress = address ? address : state.account;

  const typeFilter = useCallback((item: TankItem) => {
    switch (type) {
      case "All":
        return true
      case "Lended":
        return item.borrower?.toUpperCase() !== userAddress?.toUpperCase()
      case "OnMine":
        return item.borrower?.toUpperCase() === userAddress?.toUpperCase()
    }
    return item
  }, [type, userAddress])

  const myTanks = useMemo(() => {
    return state.tankItems
      .filter((tankItem: TankItem) => {
        return (
          tankItem.owner?.toUpperCase() ===
          userAddress?.toUpperCase() ||
          tankItem.borrower?.toUpperCase() ===
          userAddress?.toUpperCase()
        );
      })
      .filter((item: TankItem) => {
        return item.name.indexOf(filter.toLowerCase().trim()) > -1;
      })
      .filter(typeFilter);
  }, [userAddress, state.tankItems, filter]);

  return (
    <Box>
      <ProfilePanel address={userAddress}></ProfilePanel>
      <Stack
        marginTop={"30px"}
        spacing={2}
        direction={{ xs: "column", sm: "row" }}
        justifyContent="space-between"
        alignItems="center"
      >
        <TextField
          id="Search-basic"
          label="Search"
          variant="outlined"
          sx={{ width: { xs: "100%" }, maxWidth: "600px", flex: 1 }}
          value={filter}
          onChange={(e: any) => setFilter(e.target.value)}
        />
        <FormControl
          sx={{ width: { xs: "100%" }, maxWidth: "400px", flex: 1 }}
          size="medium"
        >
          <InputLabel id="sort-select">Type</InputLabel>
          <Select
            labelId="sort-select"
            id="sort-select"
            value={type}
            label="Type"
            onChange={(e: any) => setType(e.target.value)}
          >
            <MenuItem value={"All"}>All</MenuItem>
            <MenuItem value={"Lended"}>Lended</MenuItem>
            <MenuItem value={"OnMine"}>OnMine</MenuItem>
          </Select>
        </FormControl>
      </Stack>
      <Grid container spacing={2} sx={{ mt: 1 }}>
        {myTanks.map((tankItem: TankItem, index: any) => (
          <Grid
            key={index}
            item
            xs={12}
            sm={6}
            md={6}
            lg={4}
            xl={2.4}
          >
            <TankItemCard item={tankItem} />
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default MyTankPage;
