import {
  Stack,
  Box,
  Typography
} from "@mui/material"
import { useMemo, memo } from "react";
import { useParams, useNavigate } from 'react-router-dom';
import { styled } from "@mui/material/styles";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

import { useBlockchainContext } from "../../providers";
import { ActionButton2 } from "../../components/buttons";
import { AddNotification } from "../../utils/views";

import firePowerIcon from '../../assets/img/powericon.png';
import fireRateIcon from '../../assets/img/firerateicon.png';
import speedIcon from '../../assets/img/speedicon.png';
import healthIcon from '../../assets/img/healthicon.png';

export const ClassDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();

  const [state, { mintNFT }] = useBlockchainContext();
  const classInfo = state.tankClasses.find((tankClass: TankClass) => {
    return tankClass.id === Number(id);
  });

  /* ----- actions ----- */
  const handleBuy = async () => {
    try {
      var tx = await mintNFT(classInfo.id, classInfo.price);
      await tx.wait();
      AddNotification("Buy Success", tx.hash, "success");
    } catch (err: any) {
      console.log("error", err.message);
    }
  }

  // return to 404 page if item is not exist
  if (!classInfo) {
    if (state.tankClasses.length !== 0) navigate('/404');
    return (<></>);
  };

  return (
    <Box>
      <ActionButton2 onClick={() => { navigate(-1) }} sx={{ m: "5px" }}><ArrowBackIcon /> back</ActionButton2>
      <StyledPanel>
        <SubPanel>
          <Stack direction="column">
            <Row>
              <Typography variant="h4">
                {classInfo.name}
              </Typography>
            </Row>
            <Row sx={{ height: { lg: "500px" } }}>
              <Box
                component="img"
                src={classInfo.image}
                alt="DeFiTankLand"
                sx={{
                  textAlign: "center",
                  padding: { md: "50px" },
                  width: { md: "calc(100% - 100px)", xs: "100%" },
                  maxHeight: "400px",
                  mt: 3,
                  marginInline: "auto"
                }}
              />
            </Row>
          </Stack>
        </SubPanel>
        <SubPanel>
          <Stack direction="column">
            <Stack direction="column">
              <Typography variant="h5">
                Description
              </Typography>
              <Typography>
                {classInfo.description}
              </Typography>
            </Stack>

            <Row sx={{ mt: "30px" }} justifyContent="start" spacing={4}>
              <Typography>
                ID:
              </Typography>
              <Typography>
                {classInfo.id}
              </Typography>
            </Row>
            <Row sx={{ pb: "20px" }} alignItems="end">
              <Stack spacing={2} direction="column">
                <Typography>
                  Price : {classInfo.price} DFTL
                </Typography>
                <ActionButton2 onClick={handleBuy}>Buy</ActionButton2>
              </Stack>
            </Row>
            {/* Property */}
            <Row sx={{ mt: "30px" }}>
              <Typography variant="h5">
                Properties
              </Typography>
            </Row>
            <PropertyPanel id={id} />
          </Stack>
        </SubPanel>
      </StyledPanel>
    </Box>
  )
}

const PropertyPanel = ({ id }: { id: string | undefined }) => {
  const [state] = useBlockchainContext();
  const classInfo: TankClass = state.tankClasses.find((tankClass: TankClass) => {
    return tankClass.id === Number(id);
  })
  const properties = useMemo(() => {
    let properties = [];
    // fire power
    properties.push({
      icon: firePowerIcon,
      name: "Fire Power",
      value: classInfo.firePower,
      add: classInfo.firePowerAdd
    });
    //fire rate
    properties.push({
      icon: fireRateIcon,
      name: "Fire Rate",
      value: classInfo.fireRate,
      add: classInfo.fireRateAdd
    });
    //speed
    properties.push({
      icon: speedIcon,
      name: "Speed",
      value: classInfo.speed,
      add: classInfo.speedAdd
    });
    //health
    properties.push({
      icon: healthIcon,
      name: "Health",
      value: classInfo.health,
      add: classInfo.healthAdd
    });
    return properties;
  }, [classInfo])

  return (
    <Stack>
      {properties.map((property: any, index) => (
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
          sx={{
            border: "1px solid",
            borderColor: "primary.dark",
            m: 1,
            p: 1.5,
            borderRadius: 2
          }}
          key={index}
        >
          <Stack
            direction="row"
            justifyContent="flex-start"
            alignItems="center"
          >
            <Box
              component="img"
              src={property.icon}
              alt="Property"
              sx={{
                textAlign: "center",
                width: "40px",
                pr: 2
              }}
            />
            <Typography>
              {property.name}
            </Typography>
          </Stack>
          <Typography>
            {property.value}{"(+" + property.add + ")"}
          </Typography>
        </Stack>
      ))}
    </Stack>
  )
}

const StyledPanel = styled("div")(({ theme }) => ({
  borderRadius: 5,
  backgroundColor: theme.palette.secondary.main,
  border: `1px solid ${theme.palette.primary.dark}`,
  display: "flex",
  flexWrap: "wrap"
}));

const SubPanel = styled("div")(({ theme }) => ({
  [theme.breakpoints.down('sm')]: {
    minWidth: "200px",
    padding: "20px",
  },
  [theme.breakpoints.up('sm')]: {
    padding: "50px",
    minWidth: "400px",
  },
  flex: 1
}));

const Row = styled((props: any) => (
  <Stack
    direction="row"
    justifyContent="space-between"
    alignItems="center"
    {...props}
  />
))(({ theme }) => ({
  paddingBottom: "7px"
}));

export default ClassDetail;