import { useEffect, useState } from "react";
import { styled } from "@mui/material/styles";
import {
  Box,
  TextField,
  Stack,
  Paper,
  Button,
  IconButton,
} from "@mui/material";
import LoadingButton from "@mui/lab/LoadingButton";

import { AddNotification } from "../../../utils/views";
import { restApi } from "../../../providers/restapi";
import { useBlockchainContext } from "../../../providers";

const Item = styled(Paper)(({ theme }) => ({
  background: "transparent",
  padding: theme.spacing(1),
  textAlign: "center",
  boxShadow: "none",
  display: "flex",
  gap: "10px",
}));

export const EditProfile = ({
  profile,
  setModal,
  updateProfile
}: {
  profile: UserProfile;
  setModal: any;
  updateProfile: any
}) => {
  const image1 = "https://ipfs.babylonswap.finance/ipfs/QmZK72v6JWkWX153AEasaVGA5qxyt6pV9KTLwxnGMUqddK";
  const image2 = "https://ipfs.babylonswap.finance/ipfs/QmXeDJiVc3wPn3sMgYLvvJcsBAecPgoxKqKrr8TF9MxE4q";

  const [state, { }]: any = useBlockchainContext();
  const [image, _setImage] = useState(profile.image || "");
  const [selectedFile, setSeletedFile] = useState<any>(null);
  const [coverImage, _setCoverImage] = useState(profile.coverImage || "");
  const [selectedFile1, setSeletedFile1] = useState<any>(null);
  const [name, setName] = useState(profile.name || "");
  const [email, setEmail] = useState(profile.email || "player@gmail.com");
  const [password, setPassword] = useState("qwe");
  const [desc, setDesc] = useState(profile.description || "");
  const [links, setLinks]: any = useState({});
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    var bump = {};
    profile.links?.map((item: any) => {
      bump = {
        ...bump,
        [item.type]: item.href,
      };
    });
    setLinks(bump);
  }, [profile]);

  const HandleSubmit = async () => {
    try {
      if (profile.email == "") {
        // check validation
        var checkForm = new FormData();
        checkForm.append("name", name);
        checkForm.append("email", email);
        const validation: { isValid: boolean } = await restApi.checkProfile(checkForm);
        if (!validation.isValid) throw new Error("Exist Username or Email");
      }
    } catch (err: any) {
      console.log(err);
      AddNotification("Failed", err.message, "danger");
      return;
    }

    if (!selectedFile || !selectedFile1) {
      AddNotification("Waring", "Please select image", "warning");
      return;
    }
    if (name.trim() === "") {
      AddNotification("Waring", "Please enter name", "warning");
      return;
    }
    if (email.trim() === "") {
      AddNotification("Waring", "Please enter email", "warning");
      return;
    }
    if (password.trim() === "") {
      AddNotification("Waring", "Please enter password", "warning");
      return;
    }
    try {

      var newLinks: any = [];
      Object.keys(links).map((key: string) => {
        let bump = {
          type: key,
          href: links[key],
        };
        newLinks.push(bump);
      });
      setLoading(true);
      const signature = await state.signer.signMessage("welcome " + name);
      var form = new FormData();
      form.append("image", selectedFile);
      form.append("coverImage", selectedFile1);
      form.append("name", name);
      form.append("email", email);
      form.append("password", password);
      form.append("description", desc);
      form.append("links", JSON.stringify(newLinks));
      form.append("signature", signature);
      const res = await restApi.setProfile(form);
      if (res) {
        AddNotification("Success", "Successfully Updated", "success");
        updateProfile();
        setModal(false);
      }
      setLoading(false);
    } catch (err: any) {
      console.log(err);
      AddNotification("Failed", err.message, "danger");
      setLoading(false);
    }
  };

  const HandleImageChange = async (event: any) => {
    if (image) {
      setSeletedFile(null);
      URL.revokeObjectURL(image);
      _setImage("");
    }
    const newImage = event.target?.files?.[0];
    if (newImage) {
      try {
        _setImage(URL.createObjectURL(newImage));
        setSeletedFile(newImage);
      } catch (err) {
        console.log(err);
      }
    }
  };

  const HandleCoverImageChange = async (event: any) => {
    if (coverImage) {
      setSeletedFile1(null);
      URL.revokeObjectURL(coverImage);
      _setCoverImage("");
    }
    const newImage = event.target?.files?.[0];
    if (newImage) {
      try {
        _setCoverImage(URL.createObjectURL(newImage));
        setSeletedFile1(newImage);
      } catch (err) {
        console.log(err);
      }
    }
  };

  return (
    <Box sx={style}>
      <Stack spacing={2}>
        <Item>
          <Box sx={{ position: "relative" }}>
            <Box
              component="img"
              src={image || image1}
              alt=""
              sx={{
                width: "100px",
                height: "100px",
                objectFit: "cover",
                borderRadius: "50%",
              }}
            ></Box>
            <IconButton
              color="primary"
              aria-label="upload picture"
              component="label"
              sx={iconstyle}
            >
              <input
                hidden
                accept="image/*"
                type="file"
                onChange={HandleImageChange}
              />
              <Paper></Paper>
            </IconButton>
          </Box>
        </Item>
        <Item>
          <Box sx={{ position: "relative", width: "100%" }}>
            <Box
              component="img"
              src={coverImage || image2}
              alt=""
              sx={{
                width: "100%",
                height: "220px",
                objectFit: "cover",
              }}
            ></Box>
            <IconButton
              color="primary"
              aria-label="upload picture"
              component="label"
              sx={imageStyle}
            >
              <input
                hidden
                accept="image/*"
                type="file"
                onChange={HandleCoverImageChange}
              />
              <Paper></Paper>
            </IconButton>
          </Box>
        </Item>
        <Item>
          <TextField
            sx={inputstyle}
            required
            label="Name"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          <TextField
            sx={inputstyle}
            type={"email"}
            required
            label="Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
        </Item>
        <Item>
          <TextField
            sx={inputstyle}
            required
            type={"password"}
            label="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </Item>
        <Item>
          <TextField
            sx={inputstyle}
            label="Description"
            value={desc}
            onChange={(e) => setDesc(e.target.value)}
          />
        </Item>
        <Item>
          <TextField
            sx={inputstyle}
            label={"discord"}
            value={links.discord}
            onChange={(e) =>
              setLinks({ ...links, discord: e.target.value })
            }
          />
          <TextField
            sx={inputstyle}
            label={"telegram"}
            value={links.telegram}
            onChange={(e) =>
              setLinks({ ...links, telegram: e.target.value })
            }
          />
          <TextField
            sx={inputstyle}
            label={"linkedin"}
            value={links.linkedin}
            onChange={(e) =>
              setLinks({ ...links, linkedin: e.target.value })
            }
          />
          <TextField
            sx={inputstyle}
            label={"default"}
            value={links.default}
            onChange={(e) =>
              setLinks({ ...links, default: e.target.value })
            }
          />
        </Item>
        <Item>
          <LoadingButton
            variant="contained"
            sx={{ width: "100%" }}
            color={"success"}
            loading={loading}
            onClick={HandleSubmit}
          >
            Submit
          </LoadingButton>
        </Item>
      </Stack>
    </Box>
  );
};

const style = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 800,
  bgcolor: "background.paper",
  border: "2px solid background.paper",
  boxShadow: "0 3px 10px #000",
  p: 4,
  borderRadius: "5px",
};

const inputstyle = {
  width: "100%",
};

const iconstyle = {
  position: "absolute",
  top: 0,
  left: 0,
  width: "100px",
  height: "100px",
  color: "#be5c22",
};

const imageStyle = {
  position: "absolute",
  top: 0,
  left: 0,
  width: "100%",
  height: "220px",
  color: "#be5c22",
  borderRadius: "unset",
};
